export const hw4 = () => {

    document.getElementById("task4.1").onclick = function basics31() {
        alert(`–ü—Ä–∏–≤—ñ—Ç. –í–≤–µ–¥—ñ—Ç—å –¥–≤–∞ —á–∏—Å–ª–∞`);
        const firstNum = +prompt(`–í–µ–¥—ñ—Ç—å –ø–µ—Ä—à–µ —á–∏—Å–ª–æ`, '30');
        const secondNum = +prompt(`–í–≤–µ–¥—ñ—Ç—å –¥—Ä—É–≥–µ —á–∏—Å–ª–æ`, '40');

        function checkNum(firstNum, secondNum) {
            if (firstNum > secondNum) {
                return `1`;
            } else if (firstNum < secondNum) {
                return `-1`;
            }
            return `0`;
        }
        alert(checkNum(firstNum, secondNum));
    };

    document.getElementById("task4.2").onclick = function basics32() {
        const a = +prompt(`–í–≤–µ–¥—ñ—Ç—å —á–∏—Å–ª–æ`, '10');

        function factorial(a) {
            return (a != 1) ? a * factorial(a - 1) : 1;
        }
        alert(factorial(a));
    };

    document.getElementById("task4.3").onclick = function basics33() {

        const a = +prompt(`–í–≤–µ–¥—ñ—Ç—å –ø–µ—Ä—à–µ —á–∏—Å–ª–æ`, '5');
        const b = +prompt(`–í–≤–µ–¥—ñ—Ç—å –¥—Ä—É–≥–µ —á–∏—Å–ª–æ`, '3');
        const c = +prompt(`–í–≤–µ–¥—ñ—Ç—å —Ç—Ä–µ—Ç—î —á–∏—Å–ª–æ`, '10');

        function oneNum() {
            const one = [a, b, c];
            alert(one.join(''));
        }

        oneNum();
    };

    document.getElementById("task4.4").onclick = function basics34() {

        const a = +prompt(`–í–≤–µ–¥—ñ—Ç—å –¥–æ–≤–∂–∏–Ω—É`, '30');
        const b = +prompt(`–í–≤–µ–¥—ñ—Ç—å —à–∏—Ä–∏–Ω—É`, '20');

        function square(a, b) {


            if (!b) {
                const one = a * a;
                return one;
            } else {
                const two = a * b;
                return two;
            }
        }

        alert(square(a, b));
    };
};